@import "../utils/Variables.scss";

.wrapperCard {
  width: 100vw;
  display: flex;
  color: $text-color;
  flex-direction: column;
  align-items: center; /* Center items horizontally */
  justify-content: center;
  padding-top: 5%;
}
.card_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 2%;
}


.card_box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 20vw;
    height: 50vh;
    border: 1px solid $hover-color;
    border-radius: 8px;
    margin-top: 20px;
    padding: 10px;
    margin-left: 30px;
    color: $text-color;
    
  }
  
  .paragraphCard {
    font: 14px $basic-font;
    text-align: left;
    width: 80%;
    line-height: 1.5;
    padding-bottom: 2vh;

  }
  
  .learnMoreBtn
 {
  width: 150px;
    font-size: 16px;
    padding: 8px 16px;
    border: none;
    background-color:$icon-color;
    color:white;
    cursor: pointer;
  }

  .learnMoreBtn:hover {
    background-color:$hover-color;
    color: white;
    -webkit-transition: background-color 2s ease-out;
    -moz-transition: background-color 2s ease-out;
    -o-transition: background-color 2s ease-out;
    transition: background-color 2s ease-out;
  }
  .headingThree {
    font: 22px $heading-font;
  }

  @media (max-width: 820px) {
    .wrapperCard {
     padding-top: 0;
    }
    .card_container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .card_box {
      margin-top: 1vh;
      width: 70vw;
      align-self: center;
      margin-bottom: 1vh;
    }
        }

  

  
  